html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

ul {
  list-style: none;
}

button,
input,
select,
textarea {
  margin: 0;
}

html {
  box-sizing: border-box;
}

* {
  box-sizing: inherit;
}

*:before, *:after {
  box-sizing: inherit;
}

img,
embed,
object,
audio,
video {
  height: auto;
  max-width: 100%;
}

iframe {
  border: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
  text-align: left;
}

@font-face {
  font-family: DinPro;
  src: url(../fonts/DINPro.otf);
}

html,
body {
  font-family: DinPro;
}

body {
  display: flex;
  flex-flow: column nowrap;
  min-height: 100vh;
  justify-content: space-between;
}

header {
  flex: 0;
}

main {
  flex: 1;
}

footer {
  flex: 0;
}

header {
  background-color: #333333;
}

main {
  background-color: #e9e1d9;
}

.top-panel {
  display: flex;
  justify-content: space-between;
  height: 34px;
  line-height: 34px;
  padding: 0 13px;
  background-color: #000;
  color: #fbea5e;
  font-size: 23.8px;
  text-shadow: 1px 1px 1px rgba(251, 234, 94, 0.6);
  text-transform: uppercase;
}

.top-panel .left, .top-panel .right {
  flex: 0;
}

.top-panel .center {
  flex: 1;
  display: flex;
  justify-content: center;
}

.top-panel .center .jackpot-item {
  display: flex;
}

.top-panel .center .jackpot-item .jackpot-name {
  margin-right: 11px;
}

.top-panel .center .jackpot-item .jackpot-counter {
  font-size: 19.04px;
  position: relative;
}

.top-panel .center .jackpot-item .jackpot-counter .result {
  position: absolute;
  top: 0;
  left: 6px;
  letter-spacing: 12.66px;
}

.top-panel .center .jackpot-item .jackpot-counter .row {
  display: flex;
  overflow: hidden;
  background-image: linear-gradient(top, rgba(200, 200, 200, 0.2) 0%, rgba(200, 200, 200, 0.2) 40%, black 45%, black 55%, black 100%);
  background-color: #222222;
  border-radius: 3px;
}

.top-panel .center .jackpot-item .jackpot-counter .row .cell {
  width: 22.1px;
  height: 34px;
  position: relative;
  transform: matrix(1, 0, 0, 1, 0, 0);
  transition: transform 600ms linear;
}

.top-panel .center .jackpot-item .jackpot-counter .row .cell + .cell {
  box-shadow: inset 0.09em 0 0.1em 0px rgba(255, 255, 255, 0.2);
}

.top-panel .center .jackpot-item .jackpot-counter .row .cell::before {
  position: absolute;
  content: '9 \a 0 \a 1 \a 2 \a 3 \a 4 \a 5 \a 6 \a 7 \a 8 \a 9';
  line-height: 20px;
  text-align: center;
  transform: matrix(1, 0, 0, 1, 0, -12);
  transition: transform 600ms linear;
}
